import styles from "./onebiteword.module.scss";

const CardSpeak = (word) => {
  const itemWord = word;

  const playWord = () => {
    /**
     *  webview speechSynthesis  .
     *   androidChannel / iosChannel  androidChannel   flutter       .
     */
    const { androidChannel } = window;

    if (androidChannel) {
      androidChannel.postMessage(itemWord.word);
    } else {
      //   
      window.speechSynthesis.cancel();

      const speechMsg = new SpeechSynthesisUtterance();
      speechMsg.rate = 1;
      speechMsg.pitch = 1.2;
      speechMsg.lang = "en-US";
      speechMsg.text = itemWord.word;

      // SpeechSynthesisUtterance     
      window.speechSynthesis.speak(speechMsg);
    }
  };

  return (
    <button
      type="button"
      className={styles.cardSpeak}
      onClick={playWord}
    ></button>
  );
};

export default CardSpeak;
