import React, { useEffect } from "react";
import { useQuery } from "react-query";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import AOS from "aos";

import Contact from "component/contact";
import Loading from "component/loading";

import "swiper/css";
import styles from "./main.module.scss";

export const getProjectList = async () => {
  const baseUrl = process.env.REACT_APP_BASE_URL;
  const data = await fetch(baseUrl + "/api/project", {
    method: "GET",
    cache: "no-store",
    headers: {
      "Content-Type": "application/json",
    },
  }).then((res) => res.json());

  if (data.message === "OK") {
    return data.resultList;
  }
};

export default function Main() {
  const { data, isLoading } = useQuery("project", getProjectList);

  useEffect(() => {
    /// AOS 
    AOS.init();
  }, []);

  const params = {
    spaceBetween: 10,
    slidesPerView: 1,
    centeredSlides: true,
    className: styles.mainSwiper,
    autoplay: {
      delay: 1000,
      disableOnInteraction: false,
    },
    breakpoints: {
      1024: {
        autoplay: true,
        slidesPerView: 2.25,
        spaceBetween: 8,
        centeredSlides: false,
      },
    },
  };

  if (isLoading) {
    return <Loading />;
  }

  return (
    <main>
      <section className={styles.mainPage}>
        <div className={styles.container}>
          {/*    */}
          <div className={styles.mainTitle}>
            <h2 data-aos="fade-up">' '</h2>
            <p data-aos="fade-up" data-aos-delay="100">
                   
              <br />
                     
              
            </p>
            <div
              data-aos="fade-up"
              data-aos-delay="200"
              className={styles.btnArea}
            >
              <a href="/project"> </a>
              <a href="/intro"></a>
            </div>
          </div>
        </div>
      </section>

      <section>
        {/*    */}
        <Swiper {...params} modules={[Autoplay]}>
          {data &&
            data.map((item, index) => (
              <SwiperSlide key={index} className={styles.mainSlide}>
                <a href={item.readMore}>
                  <img src={item.projectMainImage} alt="  " />
                  <div className={styles.projectInfo}>
                    <h3>{item.projectName}</h3>
                    <p>{item.projectContent} </p>
                    <span>{item.projectType}</span>
                  </div>
                </a>
              </SwiperSlide>
            ))}
        </Swiper>
      </section>

      {/*   */}
      <Contact />
    </main>
  );
}
